'use strict';

// MODULES //

import ctors from '@stdlib/complex-ctors';
import DTYPES from './dtypes.js';


// MAIN //

var CTORS = [];
var i;
for ( i = 0; i < DTYPES.length; i++ ) {
	CTORS.push( ctors( DTYPES[ i ] ) );
}


// EXPORTS //

export default CTORS;
